 export default{
	 props:{
		isback:{
			type:Boolean,
			default:false,
		},
		isBackHome:{
			type:Boolean,
			default:false,
		},
		isconetnt:{
			type:Boolean,
			default:false,
		},
		isLeftContent:{
		  type:Boolean,
		  default:false,
		},
		bgColor:{
			type:String,
			default:'#39b54a',
		},
		bgSrc:{
			type:String,
			default:'',
		}
	 },
	 data(){
		 return{
			 CustomBar:this.CustomBar,
			 StatusBar:this.StatusBar,
		 }
	 },
	 
	 computed:{
		style(){
			let CustomBar = this.CustomBar;
			let StatusBar = this.StatusBar;
			let bgColor = this.bgColor;
			let bgSrc = this.bgSrc;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`
			if(bgSrc != ""){
				style = `${style}background-image:url(${bgSrc});`;
			}else{
				style = `${style}background-color:${bgColor};`;
			}
			return  style;
		} 
	 },
	 methods:{ 
		 back(){
			 uni.navigateBack({
			 	delta: 1
			 });
		 },
		 goHome(){
			 uni.navigateTo({
			 	url:'/pages/outer/index'
			 })
		 }
	 },
	 mounted() {

	 }
 }
